import React, { useState } from "react";
import {
  IonSegmentButton,
  IonLabel,
  IonSegment,
  IonRow,
  IonGrid,
  IonPage,
  IonCardContent,
  IonItem,
  IonThumbnail,
} from "@ionic/react";
import { Delivery } from "../models/delivery";

interface DeliveryItemProps {
  deliveries: Delivery[];
}

const DeliveryItem: React.FC<DeliveryItemProps> = ({ deliveries }) => {

  // const [meals, setMeals] = useState<Delivery[]>([]);

  return (
    <IonCardContent>
      {deliveries.map((delivery, i) =>
        <IonItem>
            <IonThumbnail slot="start">
              <img src={delivery.img}/>
            </IonThumbnail>
        </IonItem>
         )}
    </IonCardContent>
  );
};

export default DeliveryItem;
